

  //分享卡日期
  var getDate = function (d) {
    var dt = new Date(d);
    var m = new Array("January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December");
    var w = new Array("Monday", "Tuseday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday");
    var d = new Array("st", "nd", "rd", "th");
    var mn = dt.getMonth();
    var wn = dt.getDay();
    var dn = dt.getDate();
    var dns;
    if (wn == 0) {
      wn = 7
    }
    if (((dn) < 1) || ((dn) > 3)) {
      dns = d[3]
    } else {
      dns = d[(dn) - 1];
      if ((dn == 11) || (dn == 12)) {
        dns = d[3]
      }
    }
    return (w[wn - 1] + " . " + m[mn] + " " + dn + dns + " . " + dt.getFullYear())
  }

  //选择器
  var $ = function (selector) {
    return document.querySelector(selector)
  }

  //获取像素密度
  var getPixelRatio = function (context) {
    var backingStore = context.backingStorePixelRatio || context.webkitBackingStorePixelRatio || context.mozBackingStorePixelRatio || context.msBackingStorePixelRatio || context.oBackingStorePixelRatio || context.backingStorePixelRatio || 1;
    return (window.devicePixelRatio || 1) / backingStore
  };

  //寻找对象
  var findMomentObj = function (name, objName, key, data) { // momentId objName:commentResult or thumbResult    key:momentId

    if (!this.as.isArray(data)) {
      return false
    }
    var result = {};
    for (var i = 0; i < data.length; i++) {

      if (key == data[i][objName][name]) {
        result = data[i];
        break;
      }
    }
    return result;
  }

