  var reloadMoment = function (status) {  // default(刷新)   ,loadMore(加载更多)
    this.isGetting = true;
    var _self = this;
    if (!status) {
      status = "default";
    }

    var finalMomentList = [];
    this.H.toAjax(true).then(function (comData) { //data 公用参数
      var page, lastValue;
      if (status == "default") {
        _self.momentListPage.lastValue = 0;
        _self.momentListPage.hasMore = true;
      }
      else if (status == "loadMore" && !_self.momentListPage.hasMore) { //已无更多

        return
      }
      //var obj = dynlist(_self.activityId,_self.type,{size:50,lastValue:0});
      var obj = initMomentObj.call(_self);
      comData.data = obj;
      _self.A.post("/v1/moment/oneList", comData)
        .then(function (result) {
          if (result && result.data.state.code == 0) {
            _self.hideLoading();
            var data = result.data.data;
            _self.isGetting = false;
            var ids = [];
            //组装ids
            if (data.momentResult.moments.length) {
              data.momentResult.moments.forEach(function (value, key) {
                ids.push(value.momentId);
              })
              _self.ids = ids;

              var finalMomentList = data.momentResult.moments; // **old
              console.log("===================finalMomentList====================")
              console.log(finalMomentList);

              if (ids.length > 0) {
                var getThumbList = function () {
                  var obj = {};
                  obj = starObj(ids);
                  comData.data = obj;
                  return _self.A.post("/v1/thumb/list", comData);
                };

                var getCommentList = function () {
                  var obj = {};
                  obj = commentObj(ids);
                  comData.data = obj;
                  return _self.A.post("/v1/comment/list", comData);
                };

                //两个请求现已完成
                _self.A.all([getThumbList(), getCommentList()]).then(_self.A.spread(function (thumbResult, commentResult) {
                  if (commentResult.data.state.code == 0) {
                    var data = commentResult.data.data.commentResult;
                    data.forEach(function (value, key) {
                      finalMomentList[key]['commentResult'] = value;
                      finalMomentList[key]["commentText"] = "";// 新加 ：输入评论内容"]
                    });
                  } else {
                    _self.$emit("modalAlert", result.data.state.msg, "确定");
                  }

                  if (thumbResult.data.state.code == 0) {
                    var data = thumbResult.data.data.thumbResult;
                    data.forEach(function (value, key) {
                      finalMomentList[key]['thumbResult'] = value;
                    });
                  } else {
                    _self.$emit("modalAlert", result.data.state.msg, "确定");
                  }

                  console.warn("===================finalMomentList====================")
                  console.warn(finalMomentList);
                  _self.$nextTick(function () {
                    if (status == "default") {
                      _self.momentList = finalMomentList;
                      _self.$refs.scroll.finishPullToRefresh();
                    } else {
                      _self.momentList = _self.momentList.concat(finalMomentList);
                      _self.$refs.scroll.finishInfinite();
                    }
                  });
                })).catch(function () {
                  _self.$emit("modalAlert", "网络异常,动态列表获取失败", "确定");
                });
              } else if (ids.length == 0) {

                if (finalMomentList && status == "default") {
                  _self.$refs.scroll.finishPullToRefresh();
                } else {
                  _self.$refs.scroll.finishInfinite();
                }
              }
            }
            else {
              self.isGetting = false;
              if (status == "default") {
                _self.$refs.scroll.finishPullToRefresh();
              }
              _self.$refs.scroll.finishInfinite();

            }
          } else {
            _self.hideLoading();
            _self.$refs.scroll.finishInfinite();
            _self.$emit("$modalAlert", result.data.state.msg);
            return
          }
          if (!result) {
            return;
          }
          _self.momentListPage = result.data.data.momentResult.page;
          _self.shareType = result.data.data.momentResult.shareType;
        });
    })
  }

