      //点击图片调用微信预览
      doWechatPreview(imglist, nowIndx){
        var urls = [];
        var current = '';
        var _self = this;
        imglist.forEach(function (value, index) {
          urls.push(_self.baseUrl + value.url + '?imageView2/2/w/' + value.width + '/h/' + value.height + '/q/85!');
        });
        current = urls[nowIndx];

        var _self = this;
        //微信右上角分享
        _self.H.toAjax(true).then(function (data) {
          console.log("=============wechat====================");
          var config = {
//          debug: true,
            appId: "wx58f3e6eb9f9a1938",
            timestamp: (parseInt((new Date().getTime()) / 1000)).toString(),
            nonceStr: M.F.getId(16),
          };
          if (M.isQC || M.isDev) {
            config.appId = M.appId
          }

          var configData = {
            noncestr: config.nonceStr,
            timestamp: config.timestamp,
            url: window.location.href.split('#')[0]
          };

          var submitData = data;
          submitData.data = configData;
          //获取签名
          _self.A.post("/v1/common/configSign", submitData).then(function (result) {
            config.signature = result.data.data.sign;
            console.info(config);
            return config;
          }).then(function (result) {
            var promise = function () {
              return new Promise(function (resolve, reject) {
                var config = result;

                config.jsApiList = [
                  'hideAllNonBaseMenuItem',
                  'onMenuShareTimeline',
                  'onMenuShareAppMessage'
                ];

                _self.$wechat.config({
//              debug: true,
                  appId: config.appId,
                  timestamp: config.timestamp,
                  nonceStr: config.nonceStr,
                  signature: config.signature,
                  jsApiList: config.jsApiList
                });
                resolve();
              });
            };

            promise().then(function () {
              _self.$wechat.ready(function () {
                _self.$wechat.previewImage({
                  current: current, // 当前显示图片的http链接
                  urls: urls // 需要预览的图片http链接列表
                });
              });

              _self.$wechat.error(function (error) {
                console.log(error);
              });
            });

          });

        });
      },
