      $showEmoji(){
        var self = this;
        this.showEmoji = !this.showEmoji;
        if (!this.showEmoji) {
          return;
        }
        function findSurrogatePair(point) {
          // assumes point > 0xffff
          var offset = point - 0x10000,
            lead = 0xd800 + (offset >> 10),
            trail = 0xdc00 + (offset & 0x3ff);
          return [lead.toString(16), trail.toString(16)];
        }

        var _arr = []
        for (var i = 0; i < self.list.length; i++) {

          if (self.list[i].length > 6) {
            var arr = findSurrogatePair(self.list[i]);
            _arr.push(String.fromCharCode("0x" + arr[0], "0x" + arr[1]));
          } else {
            _arr.push(String.fromCharCode(self.list[i]));
          }
        }
        this.emotionList = _arr;
      },
