                width: width, //dom 原始宽度
                height: height, //dom 原始高度

              };
              html2canvas(shareContent, opts).then(function (canvas) {

                var body = document.getElementsByTagName("body");
                body[0].appendChild(canvas);

                // window.location.href = renderIMG ;
                var renderIMG = canvas.toDataURL()
                _self.hideLoading();
                //var renderIMG= canvas.toDataURL("image/png").replace("image/png","image/octet-stream");
                _self.dialog.renderImg = renderIMG;
                _self.showShare = true;
                _self.showShareTemp = false;
              })
            }
            var getQRcodeAndName = new Promise(function (resolve, reject) {
              _self.H.toAjax(true)
                .then(function (comData) { //data 公用参数

                  comData.data = {
