      //是否删除该动态
      onConfirmDel(){
        console.log("del内容" + m_item + " " + m_index);
        var _self = this;
        this.H.toAjax(true)
          .then(function (comData) {
            comData.data = {momentId: m_item.momentId};
            _self.A.post("/v1/moment/delete", comData).then(function (result) {
