      // ============== getAuth ===========
      $getAuth(){
        var _self = this;
        _self.H.toAjax(true).then(function (data) { //data 公用参数
          var submitData = data;
          submitData.data = {
            activityId: _self.activityId,
            type: 'attendance'
          }
          _self.A.post("/v1/auth/get", submitData).then(function (result) {
