    },
    beforeMount(){
      //如果没有用户登录则退出方法
      if(!M.F.D.get("user")) return ;

      var wechatShareData = {};
      var userCode = '';
      //微信签名信息
      var config = {
//          debug: true,
        appId: "wx58f3e6eb9f9a1938",
        timestamp : (parseInt((new Date().getTime())/1000)).toString(),
        nonceStr : M.F.getId(16),
      };
      if(M.isQC || M.isDev){
        config.appId = M.appId
      }

      var configData = {
        noncestr : config.nonceStr,
        timestamp: config.timestamp,
        url: window.location.href.split('#')[0]
      };

      var _self = this;

      _self.H.toAjax(true).then(function (data) {
        //先获取userCode,再获取分享信息,再设置configSign
        var submitData = data;
        _self.A.post("/v1/userbind/userCode",submitData).then(function (userCodeResult) {
          if(userCodeResult.data.state.code == 0) {
            userCode = userCodeResult.data.data.userCode;
            submitData.data = {
              activityId: _self.$route.query.activityId,
              userId: _self.$route.query.userId
            }
            return _self.A.post("/v1/userbind/shareUserMoment",submitData);
          }
        }).then(function (wxAccountResult) {
          if(wxAccountResult.data.state.code == 0) {
            var wxAccountResultData = wxAccountResult.data.data;

            var userName = wxAccountResultData.userName;
            var activityName = wxAccountResultData.activityName;
            var activityId = wxAccountResultData.activityId;
            var userHeadImageUrl = '';
            wxAccountResultData.userHeadImage &&
            wxAccountResultData.userHeadImage != null &&
            wxAccountResultData.userHeadImage.url &&
            wxAccountResultData.userHeadImage.url != '' &&
            wxAccountResultData.userHeadImage.url != null
              ? userHeadImageUrl = wxAccountResultData.userHeadImage.url : 'http://image.artyears.cn/template/zhishiquan.jpg';
            var day = wxAccountResultData.day;
            var userId = _self.$route.query.userId;

            wechatShareData.title = userName + '在“' + activityName + '”活动中的日常';  //标题
            wechatShareData.desc = userName + '已经坚持了' + day + '天,欢迎围观Ta的成长记录!';  //描述
            wechatShareData.link = window.location.origin + '/user/userListMeet?userId=' + userId + '&activityId=' + activityId + '&userCode=' + userCode;
            wechatShareData.imgUrl = _self.baseUrl + userHeadImageUrl;

            console.log("wechatShareData",wechatShareData);

            //获取签名
            submitData.data = configData;
            return _self.A.post("/v1/common/configSign",submitData);

          }
        }).then(function (configSignResult) {
          if(!configSignResult) {
            return;
          }
          config.signature = configSignResult.data.data.sign;
          config.jsApiList = [
            'hideAllNonBaseMenuItem',
            'onMenuShareTimeline',
            'onMenuShareAppMessage'
          ];

          _self.$wechat.config({
//            debug: true,
            appId: config.appId,
            timestamp: config.timestamp,
            nonceStr: config.nonceStr,
            signature: config.signature,
            jsApiList: config.jsApiList
          });

          _self.$wechat.ready(function () {
            //分享到朋友圈
            _self.$wechat.onMenuShareTimeline({
              title: wechatShareData.title, // 分享标题
              link: wechatShareData.link, // 分享链接
              imgUrl: wechatShareData.imgUrl, // 分享图标
            });
            //分享给微信好友
            _self.$wechat.onMenuShareAppMessage({
              title: wechatShareData.title, // 分享标题
              desc: wechatShareData.desc, // 分享描述
              link: wechatShareData.link, // 分享链接
              imgUrl: wechatShareData.imgUrl, // 分享图标
              type: 'link', // 分享类型,music、video或link，不填默认为link
              dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
            });
          });

        });
      });

    },
    mounted(){

      //显示tabBar
      this.$emit("$showToolBar", true);
      //计算宽度
      if (document.querySelector(".moment-list")) {
        var imgBoxWidth = this.imgBoxWidth = Math.floor((document.querySelector(".moment-list").clientWidth - 30 - 40) / 3 - 10);
        this.suffix = '?imageView2/1/w/' + imgBoxWidth * 2 + '/h/' + imgBoxWidth * 2;
      }
      //禁止默认事件
      document.body.onscroll = function (e) {
        e.preventDefault();
      }
      //showloading
      this.showLoading();
      var _self = this ;
      //获取传入信息
      this.userId = this.$route.query.userId;
      this.activityId = this.$route.query.activityId;
      this.type = this.$route.query.type;
      this.$route.query.from ? this.wechatFrom = this.$route.query.from : this.wechatFrom = '';
      this.A.post("/v1/moment/getResult",{
        activityId : this.activityId,
        type : this.type,
        toUserId : this.userId
      }).then(function(rs){
        if( !rs._data.isShow ){
          _self.isShowJudge = false ;
          _self.isOver = rs._data.isOver ;
          _self.isEnded = rs._data.isEnded ;
        }else{
          if(rs._data.status && rs._data.status == "actived"){
            _self.isShowJudge = true ;
            _self.isOver = rs._data.isOver ;
            _self.isEnded = rs._data.isEnded ;
            _self.status = "actived";
          }else if(rs._data.status && rs._data.status == "inactived"){
            _self.isShowJudge = true ;
            _self.isOver = rs._data.isOver ;
            _self.isEnded = rs._data.isEnded ;
            _self.status = "inactived";
          }
        }
      })

      //个人信息初始化 => hideloading
      this.$init();

      //初始化动态列表
      reloadMoment.call(this);

      //查询是否已报名
      this.$getAuth();
    },
    watch: {
      "showMenus"(status){
        if (status) {
          this.$emit("$showToolBar", false);
        } else {
          this.$emit("$showToolBar", true);
        }
      },
      "showMenus2"(status){
        if (status) {
          this.$emit("$showToolBar", false);
        } else {
          this.$emit("$showToolBar", true);
        }
      }

    },
    activated () {
      this.$refs.scroller.reset();
    },
  }



